@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
*,
*::after,
*::before {
  margin:0;
  padding:0;
  box-sizing:border-box;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}
.modal{position:absolute;top:0;left:0;bottom:0;width:100%;height:100vh;background-color:rgba(0,0,0,0.780392);display:flex;flex-direction:row;justify-content:center;align-items:center;z-index:1}.modal .content{min-height:100px;min-width:500px;background-color:#fff;border-radius:10px}.form{width:100%;padding:20px;display:flex;flex-direction:column;justify-content:center;align-items:center}.form input{width:100%;height:40px;border-radius:8px;margin-bottom:20px;border:none;background-color:#f0f4f7;outline:none;padding:0 10px;font-size:16px}.form input:focus{border:1px solid #202020}.form .dropDown{width:100%}.form .dropDown select{width:100%;height:40px;border-radius:8px;margin-bottom:20px;border:none;background-color:#f0f4f7;outline:none;padding:0 10px;font-size:16px}.form .button{width:100%;display:flex;flex-direction:row;justify-content:center;align-items:center}.form .button button{background-color:#00bf76;width:100px;height:40px;border-radius:8px;border:none;color:#fff;font-weight:bold;font-size:18px;cursor:pointer;outline:none}.form .button button:first-of-type{background-color:#00bf76;margin-right:20px}.form .button button:last-of-type{background-color:#ff7676}.form .button .disableButton{pointer-events:none;opacity:.3}.header{z-index:1;width:100%;height:60px;box-shadow:0 3px 6px 0 rgba(0,0,0,0.1);display:flex;flex-direction:row;justify-content:space-between;align-items:center;padding:0 20px;font-size:20px;font-weight:bold;position:fixed;top:0;left:0;background-color:#fff}.header .profile{cursor:pointer;display:flex;flex-direction:row;justify-content:center;align-items:center}.header .profile span{margin-right:5px}.parentContainer{overflow:auto;padding-top:90px;background-color:#f0f4f7;height:100vh;-webkit-column-count:3;column-count:3;grid-column-gap:40px;-webkit-column-gap:40px;column-gap:40px;display:flex;flex-direction:row;justify-content:center;align-items:flex-start}.parentContainer .status{width:30%;display:flex;flex-direction:column;justify-content:center;align-items:center}.parentContainer .status h2{margin-bottom:20px}.parentContainer .status .open{color:#ff7676}.parentContainer .status .inprogress{color:#f6ba34}.parentContainer .status .done{color:#00bf76}.parentContainer .statusContainer{width:400px;border-radius:10px;background-color:#fff;min-height:100px;position:relative;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:20px}.parentContainer .statusContainer .card{position:relative;width:100%;background:#f5f5f5;border-radius:10px;margin-bottom:20px;box-shadow:1px 2px 2px 0 rgba(0,0,0,0.1);padding:16px;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;font-weight:bold}.parentContainer .statusContainer .card div span{font-weight:normal}.parentContainer .statusContainer .card div:not(:last-of-type){margin-bottom:5px}.parentContainer .statusContainer .card-assignee{width:100%;display:flex;flex-direction:row;justify-content:flex-end;align-items:center}.parentContainer .statusContainer .card-assignee span{margin-right:10px}.parentContainer .statusContainer .card .editDelete{position:absolute;top:10px;right:16px;height:40px;display:flex}.parentContainer .statusContainer .card .editDelete button{outline:none;cursor:pointer;width:48px;height:48px;border:none;color:white;border-radius:100%;border:4px solid #fff;box-shadow:0 1px 2px 0 rgba(32,32,32,0.1)}.parentContainer .statusContainer .card .editDelete button:first-of-type{background-color:#268cb9;margin-right:10px}.parentContainer .statusContainer .card .editDelete button:last-of-type{background-color:#ff7676}.parentContainer .statusContainer .addTask{font-weight:bold;text-transform:uppercase;display:flex;flex-direction:row;justify-content:center;align-items:center;width:100%;height:45px;background-color:#00bf76;box-shadow:1px 2px 2px 0 rgba(0,0,0,0.1);border-radius:8px;color:#fff;cursor:pointer}.parentContainer .statusContainer .addTask .material-icons{margin-right:5px;font-weight:bold}

