*,
*::after,
*::before {
  margin:0;
  padding:0;
  box-sizing:border-box;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');